import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const store = new Vuex.Store({
state: {
    counter: 100
},
mutations: {
    increment (state, number) {
      // mutate state
      state.counter = number;
    }
  }
});




